import React, { useState } from 'react';
import { Modal, Button, Form } from 'react-bootstrap';
import { removeBuilding, removeEbell, removeCamera, removeDoor, removeGuardianlite, removeVitalsensor, removeFence, removeVCounter, removeMdet } from '../../dashboard/api/apiService';

export default function RemoveIconModal({ show, setShow, message, title, callback, clickPoint, selectObject }) {
   let iconType = '';
   if (selectObject) {
      switch (selectObject.data.type) {
         case 'building':
            iconType = '';
            break;
         case 'ebell':
            iconType = '';
            break
         case 'camera':
            iconType = '';
            break
         case 'door':
            iconType = '';
            break
         case 'guardianlite':
            iconType = '';
            break
         case 'vitalsensor':
            iconType = '';
            break
         case 'pids':
            iconType = '(Zone)';
            break
         case 'vcounter':
            iconType = 'V-Counter';
            break
         case 'mdet':
            iconType = 'M-DET';
            break
         default:
            break;
      }
   }

   const handleConfirm = () => {
      if (iconType === '') {
         removeBuilding({
            name: selectObject.data.name,
            type: selectObject.data.type,
            service_type: 'observer'
         })
      } else if (iconType === '') {
         removeEbell({
            ipaddress: selectObject.data.ipaddress
         })
      } else if (iconType === '') {
         removeCamera({
            id: selectObject.data.device_id,
            service_type: selectObject.data.service_type
         })
      } else if (iconType === '') {
         removeDoor({
            id: selectObject.data.device_id,
            service_type: selectObject.data.service_type
         })
      } else if (iconType === '') {
         removeGuardianlite({
            idx: selectObject.data.idx,
            service_type: selectObject.data.service_type
         })
      } else if (iconType === '') {
         removeVitalsensor({
            idx: selectObject.data.idx,
            service_type: selectObject.data.service_type
         })
      } else if (iconType === '(Zone)') {
         removeFence({
            id: selectObject.data.id,
         })
      } else if (iconType === 'V-Counter') {
         removeVCounter({
            id: selectObject.data.device_id,
            ipaddress: selectObject.data.ipaddress,
         })
      } else if (iconType === 'M-DET') {
         removeMdet({
            idx: selectObject.data.idx,
            ipaddress: selectObject.data.ipaddress,
         })
      }
      handleCancel();
   }
   const handleCancel = () => {
      setShow({ show: false });
   }

   return (
      <div className='modalconfirm'>
         {show ?
            <Modal show={show} onHide={handleCancel} animation={true}>
               <Modal.Header>
                  <Modal.Title>{iconType + ' '}</Modal.Title>
               </Modal.Header>
               <Modal.Body>{(iconType === '' || iconType === '' || iconType == '' || iconType == 'V-Counter' || iconType === 'M-DET') ? ' ' + iconType + ' ' + message : ' ' + iconType + ' ' + message}</Modal.Body>
               <Modal.Footer>
                  <Button variant="primary" onClick={handleConfirm}>
                     
                  </Button>
                  <Button variant="secondary" onClick={handleCancel}>
                     
                  </Button>
               </Modal.Footer>
            </Modal>
            :
            ''
         }
      </div>
   )
}