import React from 'react';
import styles from './ContextMenuModal.module.css';

export default function ContextMenuModal({
  indoor,
  handleShowModalBuilding,
  handleShowModalFloor,
  handleShowModalCamera,
  handleShowModalEbell,
  handleShowModalDoor,
  handleShowModalVitalSensor,
  handleShowModalMdet,
  handleVitalsensorScheduleGroup,
  handleVitalsensorValueGroup,
  handleOnOffVitalsensor,
  handleBreathEventLog,
  handleInterlockCamera,
  handleShowModalAckAllEvents,
  handleShowModalGuardianlite,
  handleLockDoor,
  handleUnlock10sDoor,
  handleUnLockDoor,
  handleDoorLog,
  handleAddPidsModal,
  handleRemovePidsModal,
  handleShowModalModifyBuilding,
  handleChangeIcon,
  handleRemoveIcon,
  handleRemoveFloor,
  handleClickNothing,
  handleShowDevicePopUp,
  drawStraight,
  handleShowEventDetail,
  handleShowModalVCounter
}) {
  if (indoor) {
    return (
      <>
        <div className={styles.menuMapAddIcon}>
          <div>
            <button onClick={handleShowModalCamera} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalDoor} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalVitalSensor} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalGuardianlite} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalVCounter} onContextMenu={handleClickNothing}>V-Counter </button>
          </div>
          <div>
            <button onClick={handleShowModalEbell} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalMdet} onContextMenu={handleClickNothing}>MDET </button>
          </div>
        </div>
        <div className={styles.menuMapCameraContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalAckAllEvents} onContextMenu={handleClickNothing}>   </button>
          </div>
        </div>
        <div className={styles.menuMapEbellContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}>   </button>
          </div>
        </div>
        <div className={styles.menuMapDoorContext}>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleLockDoor} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleUnlock10sDoor} onContextMenu={handleClickNothing}>  (10)</button>
          </div>
          <div>
            <button onClick={handleUnLockDoor} onContextMenu={handleClickNothing}>  </button>
          </div>
          <div>
            <button onClick={handleDoorLog} onContextMenu={handleClickNothing}>  </button>
          </div>
          <div>
            <button onClick={handleShowEventDetail} onContextMenu={handleClickNothing}>   </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}>   </button>
          </div>
          <div>
            <button onClick={handleShowModalAckAllEvents} onContextMenu={handleClickNothing}>  </button>
          </div>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
        </div>
        <div className={styles.menuMapVitalsensorContext}>
          <div>
            <button onClick={handleOnOffVitalsensor} onContextMenu={handleClickNothing}> ON/OFF</button>
          </div>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleVitalsensorScheduleGroup} onContextMenu={handleClickNothing}>  </button>
          </div>
          <div>
            <button onClick={handleVitalsensorValueGroup} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}>   </button>
          </div>
          <div>
            <button onClick={handleShowModalAckAllEvents} onContextMenu={handleClickNothing}>  </button>
          </div>
          <div>
            <button onClick={handleBreathEventLog} onContextMenu={handleClickNothing}> </button>
          </div>
        </div>
        <div className={styles.menuMapGuardianliteContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
        </div>
        <div className={styles.menuMapVCounterContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}>V-Conter </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}>V-Counter   </button>
          </div>
        </div>
        <div className={styles.menuMapMdetContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}>M-DET </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}>M-DET   </button>
          </div>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalAckAllEvents} onContextMenu={handleClickNothing}>   </button>
          </div>
        </div>
      </>
    )
  } else {
    return (
      <>
        <div className={styles.menuMapAddIcon}>
          <div>
            <button onClick={handleShowModalBuilding} onContextMenu={handleClickNothing}>  </button>
          </div>
          <div>
            <button onClick={handleShowModalCamera} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalEbell} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalGuardianlite} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalVCounter} onContextMenu={handleClickNothing}>V-Counter </button>
          </div>
          <div>
            <button onClick={handleShowModalMdet} onContextMenu={handleClickNothing}>MDET </button>
          </div>
          <li className={styles.separator} />
          <div>
            <button onClick={handleAddPidsModal} onContextMenu={handleClickNothing}>PIDS </button>
          </div>
          <div>
            <button onClick={handleRemovePidsModal} onContextMenu={handleClickNothing}>PIDS </button>
          </div>
          <li className={styles.separator} />
          <div>
            <button onClick={drawStraight} onContextMenu={handleClickNothing}> </button>
          </div>
        </div>
        <div className={styles.menuMapBuildingContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalModifyBuilding} onContextMenu={handleClickNothing}> </button>
          </div>
          <li className={styles.separator} />
          <div>
            <button onClick={handleShowModalFloor} onContextMenu={handleClickNothing}>   </button>
          </div>
          <div>
            <button onClick={handleRemoveFloor} onContextMenu={handleClickNothing}>   </button>
          </div>
          {/* <div>
            <button onClick={handleChangeIcon} onContextMenu={handleClickNothing}> </button>
          </div> */}
        </div>
        <div className={styles.menuMapCameraContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalAckAllEvents} onContextMenu={handleClickNothing}>  </button>
          </div>
        </div>
        <div className={styles.menuMapEbellContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}>  </button>
          </div>
        </div>
        <div className={styles.menuMapPidsContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalAckAllEvents} onContextMenu={handleClickNothing}>  </button>
          </div>
        </div>
        <div className={styles.menuMapGuardianliteContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}> </button>
          </div>
        </div>
        <div className={styles.menuMapVCounterContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}>V-Conter </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}>V-Counter   </button>
          </div>
        </div>
        <div className={styles.menuMapMdetContext}>
          <div>
            <button onClick={handleRemoveIcon} onContextMenu={handleClickNothing}>M-DET </button>
          </div>
          <div>
            <button onClick={handleInterlockCamera} onContextMenu={handleClickNothing}>M-DET   </button>
          </div>
          <div>
            <button onClick={() => handleShowDevicePopUp('onlyCamera')} onContextMenu={handleClickNothing}> </button>
          </div>
          <div>
            <button onClick={handleShowModalAckAllEvents} onContextMenu={handleClickNothing}>   </button>
          </div>
        </div>
      </>
    );
  }

}