import React, { useState } from 'react';
import { Modal, Button, Form } from 'react-bootstrap';
import { removeBuilding } from '../../dashboard/api/apiService';
import ColorPicker from '../../dashboard/handlers/ColorPicker';

export default function ModifyIcon({ show, setShow, message, title, inside, selectObject, canvas, color }) {
   let iconType = '';
   if (selectObject && selectObject._objects) {
      switch (selectObject.data.type) {
         case 'building':
            iconType = '';
            break;
         case 'ebell':
            iconType = '';
            break
         case 'camera':
            iconType = '';
            break
         case 'door':
            iconType = '';
            break
         case 'guardianlite':
            iconType = '';
            break
         case 'vitalsensor':
            iconType = '';
            break
         default:
            break;
      }
   }

   const handleConfirm = () => {
      if (iconType === '') {
         removeBuilding({
            name: selectObject.data.name,
            type: selectObject.data.type,
            service_type: 'observer'
         })
      } else if (iconType === '') {

      } else if (iconType === '') {

      } else if (iconType === '') {

      } else if (iconType === '') {

      } else if (iconType === '') {

      }
      handleCancel();
   }
   const handleCancel = () => {
      setShow({ show: false });
   }

   return (
      <div className='modalconfirm'>
         {show ?
            <Modal show={show} onHide={handleCancel} animation={true}>
               <Modal.Header>
                  <Modal.Title>{iconType + ' '}</Modal.Title>
               </Modal.Header>
               <Modal.Body>{iconType === '' || iconType === '' || iconType == '' ? ' ' + iconType + ' ' + message : ' ' + iconType + ' ' + message}
                  <p><label>color</label> <ColorPicker canvas={canvas} color={color} /> </p>
               </Modal.Body>
               <Modal.Footer>
                  <Button variant="primary" onClick={handleConfirm}>
                     
                  </Button>
                  <Button variant="secondary" onClick={handleCancel}>
                     
                  </Button>
               </Modal.Footer>
            </Modal>
            :
            ''
         }
      </div>
   )
}